import * as actionTypes from './actions';

const initialState = {
    details: {

    },
    token: "",
}

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.USER_TOKEN:
            return {
                ...state,
                token: action.token
            }
        case actionTypes.USER_DETAILS:
            return {
                ...state,
                details: action.user
            }
        default:
            return state;
    }
}

export default reducer;