import React, { Fragment } from "react";
import { Button, TextField, Typography, makeStyles } from '@material-ui/core';
import { Link } from "react-router-dom";
import { CLIENT_NAME } from "../utilities/constants";

const useStyle = makeStyles(theme => ({
    headingText: {
        margin: '20px 0',
        fontSize: '20px',
        fontWeight: '400',
        color: "#535766",
        letterSpacing: "1px",
        [theme.breakpoints.down('sm')]: {
            fontSize: '18px',
        },
        '&::after': {
            display: 'block',
            width: '3em',
            height: '3px',
            backgroundColor: theme.palette.primary.main,
            content: '" "',
            margin: '2px auto',
            borderRadius: '2px'
        }
    },
    textField: {
        width: '40%',
        margin: '10px 0',
        [theme.breakpoints.down('sm')]: {
            width: '85%',
        }
    },
    button: {
        margin: '10px 0',
        color: '#FFFFFF',
        backgroundColor: theme.palette.primary.main,
        '&:hover': {
            color: '#000',
            backgroundColor: theme.palette.secondary.light
        }
    },
    loginText: {
        margin: "10px",
        color: "#94969f",
        fontSize: "13.5px"
    },
    forgotPassword: {
        color: "#94969f",
        fontSize: "13.5px",
        cursor: "pointer",
        padding: "2px"
    },
    link: {
        textDecoration: "none",
        color: theme.palette.primary.main,
    }
}));

const Login = (props) => {
    const classes = useStyle();
    return (
        <Fragment>
            <Typography variant="h1" className={classes.headingText}>{`Login to ${CLIENT_NAME}`}</Typography>
            <form noValidate autoComplete="off" onSubmit={(e) => {
                e.preventDefault();
                props.login();
            }}>
                <TextField
                    required
                    label="Email"
                    id="email"
                    type="email"
                    variant="outlined"
                    className={classes.textField}
                    {...props.bindEmail}
                />
                <br />
                <TextField
                    required
                    label="Password"
                    id="password"
                    type="password"
                    variant="outlined"
                    className={classes.textField}
                    {...props.bindPassword}
                />
                <Typography variant="body1"
                    className={classes.forgotPassword}
                    onClick={() => props.passwordResetModal("forgotPasswordModal", true)}>
                    Fogot Password?
                </Typography>
                <Button
                    variant="outlined"
                    color="secondary"
                    type="submit"
                    className={classes.button}>
                    Log In</Button>
            </form>
            <Typography variant="body1" className={classes.loginText}>
                {`New to ${CLIENT_NAME}?`} <Link to="/auth/register" className={classes.link}>Create Account</Link>
            </Typography>
            {/* <Modal
                name="forgotPasswordModal"
                open={props.passwordReset}
                openModal={props.passwordResetModal}>
                <ForgotPassword onModalClose={props.passwordResetModal} />
            </Modal> */}
        </Fragment>
    )
}

export default Login;