import React, { Fragment } from "react";
import { Button, TextField, Typography, makeStyles } from '@material-ui/core';
import { Link } from "react-router-dom";
import { CLIENT_NAME } from "../utilities/constants";

const useStyle = makeStyles(theme => ({
    headingText: {
        margin: '20px 0',
        fontSize: '20px',
        fontWeight: '400',
        color: "#535766",
        letterSpacing: "1px",
        [theme.breakpoints.down('sm')]: {
            fontSize: '18px',
        },
        '&::after': {
            display: 'block',
            width: '3em',
            height: '3px',
            backgroundColor: theme.palette.primary.main,
            content: '" "',
            margin: '2px auto',
            borderRadius: '2px'
        }
    },
    textField: {
        width: '40%',
        margin: '10px',
        [theme.breakpoints.down('sm')]: {
            width: '85%',
        }
    },
    button: {
        margin: '10px 0',
        color: '#FFFFFF',
        backgroundColor: theme.palette.primary.main,
        '&:hover': {
            color: '#000',
            backgroundColor: theme.palette.secondary.light
        }
    },
    signUpText: {
        margin: "10px",
        color: "#94969f",
        fontSize: "13.5px"
    },
    link: {
        textDecoration: "none",
        color: theme.palette.primary.main,
    }
}));

const Register = (props) => {
    const classes = useStyle();
    return (
        <Fragment>
            <Typography variant="h1" className={classes.headingText}>{`Signup with ${CLIENT_NAME}`}</Typography>
            <form noValidate autoComplete="off" onSubmit={(e) => {
                e.preventDefault();
                if (props.isOTP) {
                    props.verifyRegister();
                } else {
                    props.register();
                }
            }}>
                <TextField
                    required
                    label="Name"
                    id="name"
                    type="text"
                    variant="outlined"
                    className={classes.textField}
                    disabled={props.isOTP}
                    {...props.bindName}
                />
                <TextField
                    required
                    label="Email"
                    id="email"
                    type="email"
                    variant="outlined"
                    className={classes.textField}
                    disabled={props.isOTP}
                    {...props.bindEmail}
                />
                <TextField
                    required
                    label="Mobile"
                    id="mobile"
                    type="tel"
                    variant="outlined"
                    inputProps={{ maxLength: 10 }}
                    className={classes.textField}
                    disabled={props.isOTP}
                    {...props.bindPhone}
                />
                <TextField
                    required
                    label="Password"
                    id="password"
                    type="password"
                    variant="outlined"
                    className={classes.textField}
                    disabled={props.isOTP}
                    {...props.bindPassword}
                />
                {
                    props.isOTP ?
                        <TextField
                            required
                            label="OTP"
                            id="otp"
                            type="number"
                            variant="outlined"
                            className={classes.textField}
                            {...props.bindOTP}
                        /> : null
                }
                <br />
                <Button
                    variant="outlined"
                    color="secondary"
                    type="submit"
                    className={classes.button}>
                    Submit</Button>
            </form>
            <Typography variant="body1" className={classes.signUpText}>
                Already have an account? <Link to="/auth/login" className={classes.link}>Login!</Link>
            </Typography>
        </Fragment>
    )
}

export default Register;