import React from 'react';
import Backdrop from '@material-ui/core/Backdrop';
import CircularProgress from '@material-ui/core/CircularProgress';
import { makeStyles } from '@material-ui/core/styles';
import { connect } from "react-redux";
import * as actionTypes from "../redux/actions";

const useStyles = makeStyles(theme => ({
    backdrop: {
        zIndex: theme.zIndex.drawer + 1,
        color: '#fff',
    },
}));

const SimpleBackdrop = (props) => {
    const classes = useStyles();

    const handleClose = () => {
        props.closeLoader(false);
    }

    return (
        <div>
            <Backdrop className={classes.backdrop} open={props.loader} onClick={handleClose}>
                <CircularProgress color="inherit" />
            </Backdrop>
        </div>
    );
}

const mapStateToProps = state => {
    return {
        loader: state.loader.value
    }
}

const mapDispatchToProps = dispatch => {
    return {
        closeLoader: (value) => dispatch({ type: actionTypes.LOADER, value: value }),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(SimpleBackdrop);