import React, { Fragment } from 'react';
import { Grid, Hidden, makeStyles, IconButton } from '@material-ui/core';
import { connect } from 'react-redux';
import { Route, Switch } from "react-router";
import CloseIcon from '@material-ui/icons/Close';

import { setKey } from "../utilities/cookies"
import { hitApi } from '../axios';
import { POST } from "../utilities/httpMethods";
import { validateEmail } from "../utilities/functions";
import * as actionTypes from '../redux/actions';

import Register from "./Register";
import Login from "./Login";
import useInput from "./useInput";
import Banner from '../assets/logo.svg';

const useStyle = makeStyles(theme => ({
    root: {
        backgroundColor: theme.palette.primary.main
    },
    leftContainer: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        minHeight: '100vh'
    },
    rightContainer: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        minHeight: '100vh',
        textAlign: 'center',
        padding: "0 10px",
        backgroundColor: "#FFF",
        borderRadius: "35% 10%",
        position: "relative",
        [theme.breakpoints.down('xs')]: {
            borderRadius: "0"
        },
    },
    logoImage: {
        width: '100%',
        height: 'auto',
        margin: '10px 0',
        padding: '100px',
    },
    closeImg: {
        cursor: "pointer",
        position: "absolute",
        top: "0",
        right: "0",
        margin: "15px",
        [theme.breakpoints.down('xs')]: {
            margin: "5px",
        },
    }
}));

const RegisterLogin = (props) => {
    const classes = useStyle();

    const [name, bindName] = useInput("");
    const [phone, bindPhone] = useInput("");
    const [email, bindEmail] = useInput("");
    const [password, bindPassword] = useInput("");
    const [otp, bindOTP] = useInput("");
    const [isOTP, setIsOTP] = React.useState(false);

    const [passwordReset, setPasswordReset] = React.useState(false);

    const passwordResetModal = (name, value) => {
        setPasswordReset(value)
    }

    const register = () => {
        if (name === '' || !name) {
            // openSnackbar({ message: "Please enter your Name", variant: "info" });
        } else if (!validateEmail(email) || !email) {
            // openSnackbar({ message: "Please enter valid Email address", variant: "info" });
        } else if (phone.length > 10 || phone.length < 10 || !phone) {
            // openSnackbar({ message: "Please enter valid Mobile number", variant: "info" });
        } else if (password === '' || !password) {
            // openSnackbar({ message: "Please enter Password", variant: "info" });
        } else {
            hitApi({
                url: "/user/register", method: POST,
                body: {
                    name: name,
                    email: email,
                    phone: phone,
                    password: password
                }
            }).then(result => {
                if (result) {
                    setIsOTP(true);
                }
            });
        }
    }

    const verifyRegister = () => {
        if (name === '' || !name) {
            // openSnackbar({ message: "Please enter your Name", variant: "info" });
        } else if (!validateEmail(email) || !email) {
            // openSnackbar({ message: "Please enter valid Email address", variant: "info" });
        } else if (phone.length > 10 || phone.length < 10 || !phone) {
            // openSnackbar({ message: "Please enter valid Mobile number", variant: "info" });
        } else if (password === '' || !password) {
            // openSnackbar({ message: "Please enter Password", variant: "info" });
        } else if (otp === '' || !otp) {
            // openSnackbar({ message: "Please enter Password", variant: "info" });
        } else {
            hitApi({
                url: "/user/verifyRegister", method: POST,
                body: {
                    name: name,
                    email: email,
                    phone: phone,
                    password: password,
                    otpResponse: otp
                }
            }).then(result => {
                if (result) {
                    props.history.push("/auth/login")
                }
            });
        }
    }

    const login = () => {
        if (!validateEmail(email) || !email) {
            // openSnackbar({ message: "Please enter valid Email address", variant: "info" });
        } else if (password === '' || !password) {
            // openSnackbar({ message: "Please enter Password", variant: "info" });
        } else {
            hitApi({
                url: "/user/login", method: POST,
                body: {
                    email: email,
                    password: password
                }
            }).then(result => {
                if (result) {
                    setKey("token", result.data.token);
                    props.saveUser(result.data.user)
                    props.history.push("/dashboard");
                }
            });
        }
    }

    return (
        <Fragment>
            <section className={classes.root}>
                <Grid container>
                    <Hidden only={["xs"]}>
                        <Grid item sm={6}>
                            <div className={classes.leftContainer}>
                                <div>
                                    <img src={Banner} alt="banner" className={classes.logoImage}></img>
                                </div>
                            </div>
                        </Grid>
                    </Hidden>
                    <Grid item xs={12} sm={6}>
                        <div className={classes.rightContainer}>
                            <IconButton
                                edge="start" color="primary" aria-label="search"
                                className={classes.closeImg}
                                onClick={() => props.history.push("/")}>
                                <CloseIcon />
                            </IconButton>
                            <Switch>
                                <Route
                                    path="/auth/register"
                                    render={(routerProps) => <Register
                                        {...routerProps}
                                        bindEmail={bindEmail}
                                        bindPassword={bindPassword}
                                        bindName={bindName}
                                        bindPhone={bindPhone}
                                        bindOTP={bindOTP}
                                        isOTP={isOTP}
                                        register={register}
                                        verifyRegister={verifyRegister} />} />
                                <Route
                                    path="/auth/login"
                                    render={(routerProps) => <Login
                                        {...routerProps}
                                        bindEmail={bindEmail}
                                        bindPassword={bindPassword}
                                        login={login}
                                        passwordReset={passwordReset}
                                        passwordResetModal={passwordResetModal} />} />
                            </Switch>
                        </div>
                    </Grid>
                </Grid>
            </section>
        </Fragment>
    );
}

const mapStateToProps = state => {
    console.log("Auth State", state)
    return {

    }
}

const mapDispatchToProps = dispatch => {
    return {
        saveUser: (user) => dispatch({ type: actionTypes.USER_DETAILS, user: user })
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(RegisterLogin);