import React, { useEffect } from 'react';
import { MuiThemeProvider } from '@material-ui/core/styles';
import { BrowserRouter, Route, Switch, Redirect } from 'react-router-dom';
import { Provider } from 'react-redux';

import { theme } from './theme';
import store from "./redux/store";
import { getKey } from "./utilities/cookies";

import ScrollToTop from './components/ScrollToTop';
import Backdrop from "./components/Backdrop";
import AuthComponent from "./components/Auth";


import { publicApiFunction, testAPI } from "@rohanshukla/utilities"

function App() {
    console.log(publicApiFunction())
    let data = {};
    testAPI('https://jsonplaceholder.typicode.com/todos/1')
        .then(result => {
            data = { ...result }
            console.log(data)
        })

    useEffect(() => {
        console.log("Cookie Token",getKey("token"))
    }, [])
    
    return (
        <Provider store={store}>
            <BrowserRouter>
                <ScrollToTop>
                    <MuiThemeProvider theme={theme}>
                        <Backdrop />
                        <Switch>
                            {
                                !getKey("token") ?
                                    null :
                                    <Redirect from="/auth"
                                        to="/dashboard" />

                            }
                            <Redirect exact from="/auth" to="auth/login" />
                            <Route path="/auth/login" component={AuthComponent} exact />
                            <Route path="/auth/register" component={AuthComponent} exact />
                            <Route render={(props) => (
                                <div>Page No Found 404</div>
                            )} exact />
                        </Switch>
                    </MuiThemeProvider>
                </ScrollToTop>
            </BrowserRouter>
        </Provider>
    );
}

export default App;